import express from "express";
import {
  createTeacher,
  getAllTeachers,
  getTeacherById,
  updateTeacher,
  deleteTeacher,
  updateMarks,
  getMarks,
} from "../controllers/teacher-controller";
import checkStudentAuthentication from "../helper/passport";

const router = express.Router();

// Teacher Routes
router.post("/", checkStudentAuthentication, createTeacher);
router.get("/", checkStudentAuthentication, getAllTeachers);
router.get("/:id", checkStudentAuthentication, getTeacherById);
router.put("/:id", checkStudentAuthentication, updateTeacher);
router.delete("/:id", checkStudentAuthentication, deleteTeacher);
router.patch("/:id", checkStudentAuthentication, updateMarks);
router.get("/:id/marks", checkStudentAuthentication, getMarks);

export default router;
